.portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 900px;
    margin: 0 auto;
}

.portfolio h2 {
    color: #fff;
    min-width: 100%;
    font-size: 1.4em;
    font-weight: 100;
    text-align: center;
    text-transform: uppercase;
}

.portfolio a {
    filter: invert(100%);
    width: 17%;
}

.portfolio img {
    min-width: 60px;
    padding: 3.8vw 2.5vw;
    width: 90%;
}

@media (max-width: 444px) {
    .portfolio a {
        width: 22%;
    }
}

@media (max-width: 347px) {
    .portfolio a {
        width: 40%;
    }
}
